body ::-webkit-input-placeholder {
  /* WebKit browsers */
  /* font-family: "Source Sans Pro", sans-serif; */
  color: white;
  font-weight: 300;
  background-image: none !important;
}
body :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  /* font-family: "Source Sans Pro", sans-serif; */
  color: white;
  opacity: 1;  z-index: 100 !important;
  font-weight: 300;
}
body ::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  /* font-family: "Source Sans Pro", sans-serif; */
  color: white;
  opacity: 1;
  font-weight: 300;
}
body :-ms-input-placeholder {
  /* Internet Explorer 10+ */
  /* font-family: "Source Sans Pro", sans-serif; */
  color: white;
  font-weight: 300;
}
.wrapper {
  /* background: #f7c115; */
  /* background: linear-gradient(to bottom right, #f7c115 0%, #f7c115 100%); */
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  height: calc(100vh - 75px);
  margin-top: -250px;
  overflow: hidden;
  z-index: 1;
}
.wrapper.form-success .form-container h1 {
  -webkit-transform: translateY(85px);
          transform: translateY(85px);
}
.form-container {
  background: linear-gradient(to bottom right, #f7c115 0%, #f7c115 100%);
  max-width: 400px;
  margin: 10% auto;
  padding: 3% 1%;
  border-radius: 1%;
  -webkit-box-shadow: 2px -1px 21px -7px rgba(0,0,0,0.47);
  -moz-box-shadow: 2px -1px 21px -7px rgba(0,0,0,0.47);
  box-shadow: 2px -1px 21px -7px rgba(0,0,0,0.47);
  text-align: center;
}
.form-container h1 {
  font-size: 40px;
  transition-duration: 1s;
  transition-timing-function: ease-in-put;
  font-weight: 200;
}
form {
  padding: 20px 0;
  position: relative;
  z-index: 2;
}
form input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background-color: rgba(255, 255, 255, 0.2);
  width: 250px;
  border-radius: 3px;
  padding: 10px 15px;
  margin: 0 auto 10px auto;
  display: block;
  text-align: center;
  font-size: 18px;
  color: white;
  transition-duration: 0.25s;
  font-weight: 300;
}
form input:hover {
  background-color: rgba(255, 255, 255, 0.4);
}
form input:focus {
  background-color: white;
  width: 300px;
  color: #f7c115;
}
form button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  background-color: white;
  border: 0;
  padding: 10px 15px;
  color: #f7c115;
  border-radius: 3px;
  width: 250px;
  cursor: pointer;
  font-size: 16px;
  transition-duration: 0.25s;
}
form button:hover {
  background-color: #f5f7f9;
}
